/* page styles */
html {
  overflow-y: scroll;
}

.container {
  margin: auto;
  max-width: 800px;
  width: 80%;
}

pre {
  background: #eee;
  border: 1px solid #ccc;
  border-radius: .25em;
  padding: .75em;
}


/* clamp styles */
.clamp-lines {
  border: 1px solid orange;
  border-radius: .25em;
  font-family: sans-serif;
  margin: 1em;
  padding: .75em;
}

.clamp-lines__button {
  background: none;
  border: 0;
  color: red;
  cursor: pointer;
  padding: 0;
}

.clamp-lines__button:hover {
  border-bottom: 1px solid red;
}

/* clamp custom styles */
.clamp-lines.custom-class {
  background: ivory;
}

.clamp-lines.custom-class .clamp-lines__button {
  background: #f5f5f5;
  border: 1px solid #ddd;
  display: block;
  margin: .75em auto 0;
  padding: .5em;
  text-align: center;
  width: 100%;
}

.clamp-lines.custom-class .clamp-lines__button:hover {
  background: #eee;
}


